.container {
display: flex;
flex-wrap: wrap;
justify-content: center;
align-items: stretch; /* Ensures all cards match the tallest one */
}

.card {
display: inline-flex;
justify-content: stretch;
align-items: center;
flex-direction: column;
width: 350px;
max-width: calc(100% - 20px - 20px);
margin: 20px;
background: var(--background);
border-radius: var(--rounded);
overflow: hidden;
box-shadow: var(--shadow);
vertical-align: top;
}


.card[data-style="small"] {
  width: 150px;
}

.card-image {
  // aspect-ratio: 2 / 2;  /* Carmelo: it was 3 / 2 */
  // object-fit: cover;
  width: 50%;
  margin-top: 10px;
}

.card-image img {
  aspect-ratio: 2 / 2;  /* Carmelo: it was 3 / 2 */
  object-fit: cover;
  width: 100%;
  // box-shadow: var(--shadow);
}

.card-text {
  display: inline-flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
  gap: 20px;
  max-width: 100%;
  padding: 10px;
}

.card-text > * {
  margin: 0 !important;
}

.card-title {
  font-family: var(--heading);
  font-weight: var(--semi-bold);
  font-size: 0.8rem;
}

.card-subtitle {
  margin-top: -10px !important;
  font-style: italic;
  font-size: 0.7rem;
}

.card-disabled {
  opacity: 0.5;
  pointer-events: none;
  filter: grayscale(100%);
}

.card-ribbon {
  font-size: 0.5rem;
  margin-top: -20px !important;
}